   font-size: 35px;
   font-weight: bold;
   text-align: center;
   text-decoration: line-through;
   color: #e20000;
   /* background-color: #000000; */
   padding: 50px;
}

.day-container {
   margin-bottom: 50px;
}

.day-title {
   font-size: 28px;
   font-weight: bold;
   margin-bottom: 30px;
   text-align: center;
   color: #bd0000;
}

.completedStatus-day-title {
   font-size: 28px;
   font-weight: bold;
   margin-bottom: 30px;
   text-align: center;
   color: #818181;
   text-decoration: line-through;
}

.exercises-container {
   display: flex;
   flex-wrap: wrap;
   /* gap: 20px; */
   justify-content: center;
   /* background-color: #282c34; */
}

.completedStatus-exercises-container {
   display: flex;
   flex-wrap: wrap;
   gap: 0px;
   justify-content: center;
  
}

.exercise-container {
   width: calc(20% - 10px);
   background-color: #e3e3e3;
   padding: 16px;
   /* border: 4px solid rgb(20, 21, 11); */
   border-right: 4px solid black;
   border-left: 4px solid black;
   border-radius: 4px;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
}

.completedStatus-exercise-container {
   width: calc(20% - 10px);
   background-color: #aaaaaa;
   padding: 16px;
   /* border: 4px solid rgb(48, 194, 63); */
   border: 4px solid #092309;
   border-radius: 4px;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
}

.exercise-title {
   font-size: 26px;
   font-weight: bold;
   margin-bottom: 8px;
   text-align: center;
   color: rgb(236, 0, 0);
   text-transform: uppercase;
}

.completedStatus-exercise-title {
   font-size: 26px;
   font-weight: bold;
   margin-bottom: 8px;
   text-align: center;
   color: rgb(197, 0, 0);
   text-transform: uppercase;
   text-decoration: line-through;
}

.modification-mode-container {
   margin-top: 16px;
   margin-bottom: 8px;
   text-align: center;
}

.modification-mode-label {
   font-size: 14px;
}

.modification-mode-checkbox {
   margin-right: 8px;
   vertical-align: middle;
}

.modification-mode-checkbox-label {
   font-size: 16px;
   vertical-align: middle;
   color: #8f0c00;
}

.completedStatus-modification-mode-checkbox-label {
   font-size: 16px;
   vertical-align: middle;
   color: #000000;
}

.exercise-table {
   width: 100%;
   margin-top: 16px;
   border-collapse: collapse;
   background-color: #e7e7e7;
   color: #000000;
}

.completedStatus-exercise-table {
   width: 100%;
   margin-top: 16px;
   border-collapse: collapse;
   background-color: #333;
   text-decoration: line-through;
   color: #dddddd;
}

.exercise-table th,
.exercise-table td {
   font-size: 18px;
   padding: 8px;
   text-align: center;
   border: 1px solid #898989;
}

.exercise-table th {
   background-color: #adadad;
}

.exercise-table td {
   background-color: #cccccc;
}

.completedStatus-exercise-table td {
   text-align: center;
}

.button {
   margin-top: 8px;
   padding: 8px 16px;
   background-color: #eaeaea;
   color: #fff;
   border: none;
   border-radius: 4px;
   cursor: pointer;
   font-size: 16px;
}

.completedStatus-button {
   margin-top: 8px;
   padding: 8px 16px;
   background-color: #000000;
   color: #d8d8d8;
   border: none;
   border-radius: 4px;
   cursor: pointer;
   font-size: 16px;
}

.button:hover {
   background-color: #b70000;
}

.completedStatus-button:hover {
   background-color: #972718;
}

.bottom-buttons {
   margin-top: 150px;
   display: flex;
   flex-wrap: wrap;
   gap: 40px;
   justify-content: center;
   padding-bottom: 40px;
}

.complete-archive-button {
   margin-top: 20px;
   padding: 1px 16px;
   background-color: #0e4011;
   color: #fff;
   border: none;
   border-radius: 4px;
   cursor: pointer;
   height: 40px;
   font-size: 20px;
   font-weight: bold;
}

.complete-archive-button:hover {
   background-color: rgb(169, 169, 169);
   color: #0e4011;
   font-weight: bold;
}

.archive-button {
   margin-top: 20px;
   padding: 8px 16px;
   background-color: rgb(167, 167, 167);
   color: #000000;
   border: none;
   border-radius: 4px;
   cursor: pointer;
   height: 40px;
   font-size: 20px;
   font-weight: bold;
}

.archive-button:hover {
   background-color: rgb(210, 205, 205);
   color: rgb(235, 0, 0);
   font-weight: bold;
}

.delete-button {
   margin-top: 20px;
   padding: 8px 16px;
   background-color: rgb(159, 159, 159);
   color: #000000;
   border: none;
   border-radius: 4px;
   cursor: pointer;
   height: 40px;
   font-size: 20px;
   font-weight: bold;
}

.delete-button:hover {
   color: rgb(206, 0, 0);
   font-weight: bold;
   background-color:rgb(210, 205, 205);
}

.progress-bar-container {
   position: relative;
   min-height: 100vh;
   width: auto;
}

.sticky-footer {
   position: sticky;
   bottom: 0;
   z-index: 100;
}

.custom-progress-bar {
   height: 30px;
   margin-top: 20px;
   border-radius: 4px;
   overflow: hidden;
   box-shadow: 0 2px 4px rgba(152, 152, 152, 0.2);
   position: relative;
}

.progress-bar {
   height: 100%;
   background-color: #ec3300;
   transition: width 0.5s ease-in-out;
   background-image: linear-gradient(
      45deg,
      rgba(255, 255, 255, 0.15) 25%,
      transparent 25%,
      transparent 50%,
      rgba(255, 255, 255, 0.15) 50%,
      rgba(255, 255, 255, 0.15) 75%,
      transparent 75%,
      transparent
   );
   background-size: 1rem 1rem;
   animation: progress-bar-stripes 1s linear infinite;
   position: relative;
}

.progress-bar-percentage {
   position: absolute;
   top: 50%;
   right: 0;
   transform: translate(0, -50%);
   display: flex;
   align-items: center;
   justify-content: center;
   /* padding: 0 10px; */
   font-size: 14px;
   color: #000000;
   white-space: nowrap;
   font-weight: bold;
   font-size: 20px;
}

@keyframes progress-bar-stripes {
   from {
      background-position-x: 1rem;
   }
   to {
      background-position-x: 0;
   }
